<template>
<v-app>
  <v-container>
    <h2 v-html="message" class="center-text"></h2>
  <div class="grid-container">
    <div 
      v-for="item in homeNavItems" 
      :class= getCssClass(item.color)
      :key="item.name"
      @click="goToPath(item.path)"
      >
      {{item.name}}
      </div>
  </div>
  </v-container>
</v-app>
</template>

<script>

  export default {
    components: {
    },
    data () {
      return {
        message: "Home Page"
      }
    },
    computed: {
      homeNavItems: function() {
        return this.$store.getters.homeNavItems
      }
    },
    methods: {
      goToPath (path) {
        this.$router.push(path)
      },
    getCssClass(color) {
        return "grid-item pointer " + color
      } 
    },
  }
</script>

<style>

.grid-container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(100px, 1fr));
  grid-gap: 5px;
  grid-auto-rows: minmax(100px, auto);
  align-content: stretch;
}

.grid-item {
  padding:10px;
}

.pointer {
  cursor: pointer;
}

.center-text {
  text-align: center
}

</style>
