import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    sideNav: false,
    navItems: [
      {
        name: 'About',
        path: 'about',
        menus: ['all'],
        auth: ['all'],
        color: 'red'
      },
      {
        name: 'Home',
        path: '/',
        menus: ['all'],
        auth: ['all'],
        color: 'blue'
      },
      {
        name: 'Chat',
        path: 'chat',
        menus: ['drawer','home'],
        auth: ['all'],
        color: 'yellow'
      },
      {
        name: 'Documents',
        path: 'documents',
        menus: ['toolbar','drawer','home'],
        auth: ['all'],
        color: 'green'
      },
      {
        name: 'Teams',
        path: 'teams',
        menus: ['toolbar','drawer','home'],
        auth: ['all'],
        color: 'purple'
      },
    ]
  },
  getters: {
    sideNav: state => state.sideNav,
    menuItems: state => state.menuItems,
    toolbarNavItems: function(state) {
      return state.navItems.filter(e => e.menus.includes('toolbar') || e.menus.includes('all'))
    },
    homeNavItems: function(state) {
      return state.navItems.filter(e => e.menus.includes('home') || e.menus.includes('all'))
    },
    drawerNavItems: function(state) {
      return state.navItems.filter(e => e.menus.includes('drawer') || e.menus.includes('all'))
    },
  },
  mutations: {
    sideNav (state, payload) {
      state.sideNav = payload
    }

  },
  actions: {

  }
})
