<template>
      <v-bottom-nav :value='true' fixed color="white" height="50px">
        <v-btn
        flat
        router
        :to="'chat'"
      >
        Chat
      </v-btn>
      <v-tooltip top>
      <v-btn
        slot="activator" 
        flat
        v-show='showBack'
        @click="returnBack"
      >
        <v-icon large dark>arrow_back</v-icon>
      </v-btn>
      <span>Navigate Back</span>
      </v-tooltip>
      <v-tooltip top>
      <v-btn
        slot="activator" 
        flat
        v-show='showBack'
        @click="returnHome"
      >
      <v-icon large dark v-if="!loading">home</v-icon>
      <v-progress-circular
        indeterminate
        color="primary"
        v-if="loading">
      </v-progress-circular>
      </v-btn>
      <span>Home</span>
      </v-tooltip>


      <v-btn
        flat
        router
        :to="'announcements'"
      >
        Announcements <span v-if="announcementCountNotViewed"> {{announcementCountNotViewed}} new </span>
      </v-btn>
      <!-- {{ user }} -->
     </v-bottom-nav>

</template>

<script>
export default {
  data () {
    return {
      showBack: true,
      loading: false,
      announcementCountNotViewed: false,
      announcementCountNotViewed: 5
    }
  },
  methods: {
    returnBack () {
      this.$router.go(-1)
    },
    returnHome () {
      this.$router.push('/')
    }
  } 
}
</script>
